import './styles.css';
import { NavBar } from '../../components/NavBar';
import { EvolutionInterface } from '../../repositories/EvolutionInterface';
import { EvolutionCard } from '../../components/EvolutionCard';
import { useNavigate, useParams } from 'react-router-dom';
import { api } from '../../services/api';
import { BiSearchAlt } from 'react-icons/bi';
import Swal from 'sweetalert2';
import { useEffect, useState } from 'react';

export function Demands () {

  let navigate = useNavigate();
  const token = localStorage.getItem('access_token');
  const [data, setData] = useState<EvolutionInterface[] | null>(null);
  const [search, setSearch] = useState("");

  useEffect(() => {
      api.get(`/evolution/demands`, {
        headers: {
          Authorization: `Bearer ${token}`,
        }
      })
      .then(response => {
        setData(response.data)
      })
  }, [])

 
  async function handleDeleteEvolution(id: string){
    Swal.fire({
      title: 'Voc tem certeza que deseja deletar tal evoluo?',
      showDenyButton: true,
      confirmButtonText: 'Sim, deletar',
      denyButtonText: 'No, cancelar',
      customClass: {
        actions: 'my-actions',
        confirmButton: 'confirm',
        denyButton: 'order-3',
        title: 'title'
      }
    }).then(async (result) => {
      if(result.isConfirmed){
        await api.delete(`/evolution/${id}`, {
          headers: {
            Authorization: `Bearer ${token}`
          }
        })
        .then(() => {
          Swal.fire({
            title: 'Dados deletados com sucesso!',
            icon: 'success',
            customClass: {
              title: 'title'
            },
            confirmButtonColor: '#58AA93'
          })

          navigate(0);
        })
      }else if(result.isDenied){
        
        Swal.fire({
          title: 'Evoluo excluda!',
          customClass: {
            title: 'title'
          },
          confirmButtonColor: '#58AA93'
        })
      }
    })
    

   /*   */
  }


  return(
    <div className='demands'>
      <NavBar page='Evolues'/>

      <section className='section-demands'>
      
        <h1 className='title-demands'>Demandas - Centro POP</h1>

        <input className='search-demand' type="text" placeholder='Nome do assistido' value={search} onChange={(e) => setSearch(e.target.value.toUpperCase())}/>
        <BiSearchAlt className='icon-demand'/>


        {
        data?.map(repo => {
          if(repo.assisted?.name.toUpperCase().includes(search)){
            return (
              <EvolutionCard  
                key={repo.id} 
                title={repo.assisted?.name.toUpperCase()}
                data={repo.data} 
                demand={repo.demand}
                description={repo.description}
                status={repo.status}
                quantity={repo.quantity}
                assisted={repo.assistedId} 
                id={repo.id}
                perfilOn={true}
                del={() => handleDeleteEvolution(String(repo.id))}
              />
            )
          }
        })

        }
        
           
      </section>
    </div>
  )

}